class TabContentContainer {

    id:string;

    element:HTMLElement;

    public show () {
        this.element.classList.add('visible');
    }

    public hide () {
        this.element.classList.remove('visible');
    }

}

class TabsContainer {

    element:HTMLElement;

    links:HTMLElement[] = [];

    contents:TabContentContainer[] = [];

}

let tabs:TabsContainer[] = [];

function onTabItemClicked () {
    let id:string = this.element.getAttribute('data-target');
    location.hash = this.element.getAttribute('data-target');

    this.container.links.forEach((item:HTMLElement) => {
        item.getAttribute('data-target') == id ? item.classList.add('active') : item.classList.remove('active');
    });

    this.container.contents.forEach((item:TabContentContainer) => item.id == id ? item.show() : item.hide());
}

function createTabsContainer (element:HTMLElement) {
    let container:TabsContainer = new TabsContainer();
    let links:NodeListOf<Element> = element.querySelectorAll('.tabs-navigation > *');
    let defaultItem:HTMLElement = null;

    const hash:string = location.hash.substr(1, location.hash.length);

    container.element = element;

    for (let i = 0; i < links.length; i++) {
        let tabItem:HTMLElement = links.item(i) as HTMLElement;

        if (!tabItem.classList.contains('dropdown')) {
            let tabContentContainer:TabContentContainer = new TabContentContainer();
            let id:string = tabItem.getAttribute('data-target');
            let selector:string = '.tabs-contents > [data-tab="' + id + '"]';

            container.links.push(tabItem);
            tabContentContainer.element = element.querySelector(selector) as HTMLElement;
            tabContentContainer.id = id;
            container.contents.push(tabContentContainer);

            tabItem.addEventListener('click', onTabItemClicked.bind({
                element: tabItem,
                container: container
            }));


            if (tabItem.hasAttribute('data-default') && hash.length === 0) {
                defaultItem = tabItem;
            }
            if (hash.length > 0 && hash == id) {
                defaultItem = tabItem;
            }
        } else {
            /*  */
        }
    }

    tabs.push(container);

    defaultItem.click();
}

export default function createTabs () {

    const tabsContainers:NodeListOf<Element> = document.querySelectorAll('.tabs');

    for (let i = 0; i < tabsContainers.length; i++) {
        createTabsContainer(tabsContainers.item(i) as HTMLElement);
    }

}
